import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'ks-contact-buttons',
})
export class ContactButtons {

  @Prop() phone: string = "000000000";
  @Prop() email: string;

  render() {
    return[
        <a href={"tel:" + this.phone} class="uk-button uk-button-default uk-border-pill" style={{margin: "2px"}}>{this.phone}</a>,
        <a href={"mailto:" + this.email} class="uk-button uk-button-default uk-border-pill" style={{margin: "2px"}}>{this.email}</a>
    ];
  }
}